import React, { Component, createRef } from 'react';
import axios from 'axios';

import './Recipes.scss';
import Recipe from '../../components/Recipe/Recipe';
import Spinner from '../../components/Spinner/Spinner';
import Button from '../../components/Button/Button';

class Recipes extends Component {
  state = {
    recipes: [],
    numberOfResults: 0,
    resultPerPage: 6,
    page: 0,
    start: 0,
    end: 6,
    noRecipe: false
  }

  constructor(props) {
    super(props);
    this.node = createRef();
  }
  
  async componentDidMount() {
    this.scrollBehaviour();
    const recipe = this.props.match.params.category;

    if (recipe.trim() === 'pizza' || recipe.trim() === 'pasta') {
      const { data } = await axios.get(`https://forkify-api.herokuapp.com/api/search?&q=${recipe}`);
      this.setState({ recipes: data.recipes, numberOfResults: data.recipes.length, noRecipe: false });
    } else {
      this.setState({ noRecipe: true });
    }
  }

  previousResults = () => {    
    this.setState(state => ({
      page: state.page - 1,
      start: state.start - state.resultPerPage,
      end: state.end - state.resultPerPage
    }));
    this.scrollBehaviour();
  }

  nextResults = () => {    
    this.setState(state => ({
      page: state.page + 1,
      start: (state.page + 1) * state.resultPerPage,
      end: state.resultPerPage * (state.page + 2)
    }));
    this.scrollBehaviour();
  }

  scrollBehaviour = () => {
    this.node.current.scrollIntoView({ behaviour: 'smooth' });
  }

  render() {
    const { recipes, noRecipe, start, end, numberOfResults } = this.state;
    const category = this.props.match.params.category;

    const showPreBtn = start !== 0;
    const showNextBtn = end <= numberOfResults;

    return(
      <div className='recipes' id='gotoTop' ref={this.node}>
        {noRecipe 
          ? <p className='noRecipe'>recipe not found</p> 
          : <>
            <div className='recipeTitle'>
              <h3>{category}</h3>
              <p>Explore all new and delicious {category}</p>
            </div>
            <div>
              {
                recipes.length > 0 
                  ? <div className="recipeList">
                      {recipes.slice(start, end).map(recipe => (
                        <Recipe key={recipe.recipe_id} recipe={recipe}/>
                      ))}
                    </div>
                  : <Spinner />
              }
            </div>
            <div className="pageButtons">
              {showPreBtn && <Button click={this.previousResults} className='pagination'>&#10094; Prev</Button>}
              {showNextBtn && <Button click={this.nextResults} className='pagination'>Next &#10095;</Button>}
            </div>
          </>}
      </div>
    );
  }
}

export default Recipes;