import React from 'react';
import { withRouter, NavLink } from 'react-router-dom';
import './Recipe.scss';

import { recipePrice, limitTitle } from '../../utility/recipe';

const Recipe = ({ history, match, recipe }) => {
  const currentUrl = match.url;

  return(
    <div className='recipeContainer' onClick={() => history.push(`${currentUrl}/${recipe.recipe_id}`)}>
      <div className='recipe'>
        <div className='imageContainer' style={{backgroundImage: `url(${recipe.image_url})`}}/>
        <div className='recipe__details'>
          <h3>{limitTitle(recipe.title)}</h3>
          <div className='recipe__info'>
            <span className='info'>{recipe.publisher}</span>
            <span className='info'>$ {recipePrice(recipe.recipe_id)}</span>
            <NavLink to={`${currentUrl}/${recipe.recipe_id}`}  className='info more__info' title='view full details of this recipe'>More Info</NavLink>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Recipe);